import React, {Component} from "react";
import {withRedux} from "../../store/wrapper";
import PropTypes from "prop-types";

class FormInfoStrap extends Component{

  render() {
    let strapContent = '';
    switch (this.props.formStep) {
      case 1:{
        strapContent = 'Uzupenij szczegy dotyczce Twoich rzeczy. Dziki temu bdziemy wiedzie komu najlepiej je przekaza.';
        break;
      }
      case 2:{
        strapContent = 'Wszystkie rzeczy do oddania zapakuj w 60l worki.  Dokadn instrukcj jak poprawnie spakowa rzeczy znajdziesz TUTAJ.';
        break;
      }
      case 3:{
        strapContent = 'Jeli wiesz komu chcesz pomc, moesz wpisa nazw tej organizacji w wyszukiwarce. Moesz te filtrowa organizacje \n' +
          'po ich lokalizacji bd celu ich pomocy.';
        break;
      }
      case 4:{
        strapContent = 'Podaj adres oraz termin odbioru rzeczy.';
        break;
      }
      default: {
        strapContent = 'Uzupenij szczegy dotyczce Twoich rzeczy. Dziki temu bdziemy wiedzie komu najlepiej je przekaza.';
      }
    }

    let content = null;
    if(this.props.formStep >= 1 && this.props.formStep < 5){
      content = (<div className={'form__info__container'}>
        <p className={'form__info__header'}>Wane!</p>
        <p className={'form__info__desc'}>{strapContent}</p>
      </div>)
    }
    return content
  }
}

FormInfoStrap.propTypes = {
  formStep: PropTypes.number
};

const FormInfoStrapHoc = withRedux(FormInfoStrap);
export {FormInfoStrapHoc as FormInfoStrap}